                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd"><label for="" class="weui-label">省份</label></div>
                    <div class="weui-cell__bd">
                        <input class="weui-input" type="text" placeholder="请选择所在省份" readOnly = 'true' id='province'/>
                    </div>
